  var line_y = 4;
  cat_graph_group
    .append('line')
    .attr('x1', 0)
    .attr('x2', bar_width)
    .attr('y1', line_y)
    .attr('y2', line_y)
    .attr('stroke', 'blue')
    .attr('stroke-width', 1)
